body {
  height: 100%;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Kato';
}

.ul{
  list-style-type: none;
}

.img{
  width: 20%;
}

.split{
  border: 10px solid #fff;
}

/*.li-edits{
  z-index: 1;
  //height: 20px;
}*/

.TextDived{
    width: 100%;
    padding: 1%;
    border: 1px solid #fff;   
    font-size: 1.5rem; 
    margin: 2%;
}

html{
  height: 100%;
  margin: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'Kato';
  src: url('./fonts/KATO____.ttf'); /* IE9 Compat Modes */
}

@font-face {
  font-family: 'Okami';
  src: url('./fonts/Okami-Outline.otf'); /* IE9 Compat Modes */
}


